import styled from "styled-components"

export const Container = styled.div`
	background-color: #fff;
	box-shadow: 0px 0.3px 10px 0.3px rgba(68, 68, 68, 0.15);
`

export const SubContainer = styled.div`
	margin: 40px 0;
	padding: 48px;

	.row {
		align-items: center;
	}

	@media (max-width: 992px) {
		padding: 20px;
	}
`
export const ContainerDescription = styled.div`
	button {
		margin-top: 64px;
	}

	h2 {
		font-family: "Montserrat", sans-serif;
		font-size: 38px;
		margin: 0;

		span {
			color: #19b6fd;
			font-weight: 700;
		}
	}

	@media (max-width: 1024px) {
		h2 {
			font-size: 24px;
		}

		button {
			margin-top: 16px;
		}
	}

	@media (max-width: 576px) {
		margin-top: 24px;
		text-align: center;
	}
`
export const ContainerSubDescription = styled.div`
	font-family: "Montserrat", sans-serif;
	font-size: 16px;
	color: #444;
	font-weight: 300;
	margin-top: 16px;
`

export const ImageContainer = styled.div`
	img {
		border-radius: 8px;
	}
`
