import React, { useState } from "react"

import {
	Container,
	SubContainer,
	ImageContainer,
	ContainerDescription,
	ContainerSubDescription,
} from "./styles"

import Button from "../Button"

import Pic1 from "../../../assets/carousel/img-reuniao-compress-min.jpg"

import { translate } from "../translate"
import { FaRegNewspaper } from "react-icons/fa"
import FormNewsletter from "../Form/FormNewsletter"

export default function BookCall() {
	const [showModal, setShowModal] = useState(false)

	const openModal = () => setShowModal(true)
	const closeModal = () => setShowModal(false)

	return (
		<>
			{showModal && (
				<FormNewsletter showModal={showModal} onCloseModal={closeModal} />
			)}
			<div className="row">
				<div className="col-md">
					<Container>
						<SubContainer>
							<div className="row">
								<div className="col-sm">
									<ImageContainer>
										<img
											className="d-block w-100"
											src={Pic1}
											alt="Agendar reunião"
										/>
									</ImageContainer>
								</div>
								<div className="col-sm">
									<ContainerDescription>
										<h2>
											{translate("meet.title")}
											<span> {translate("meet.customTitle")}</span>
										</h2>
										<ContainerSubDescription>
											{translate("meet.description")}
										</ContainerSubDescription>
										<a
											href="https://outlook.office365.com/owa/calendar/KivalitaConsulting1@kivalita.com.br/bookings/"
											target="_blank"
										>
											<Button
												title={translate("meet.button.schedule")}
												bgColor={"#19B6FD"}
												fontFamily={"Poppins"}
												fontSize={17}
												fontWeight={600}
												textColor={"#FFF"}
												padding={"20px"}
												width={"100%"}
											/>
										</a>
										<br />
										<a onClick={openModal}>
											<Button
												// title={translate("meet.button.schedule")}
												title={"Newsletter"}
												bgColor={"#F4BF8C"}
												fontFamily={"Poppins"}
												fontSize={17}
												fontWeight={600}
												textColor={"#FFF"}
												padding={"20px"}
												width={"100%"}
											/>
										</a>
									</ContainerDescription>
								</div>
							</div>
						</SubContainer>
					</Container>
				</div>
			</div>
		</>
	)
}
