import React from "react"
import { PageProps } from "gatsby"

import { ISolutionPage, IBreadCrumbOption } from "../../interfaces"
import Banner from "../../components/utils/Banner"
import BookCall from "../../components/utils/BookCall"
import Layout from "../../components/Layout"
import SectionHighlight from "../../components/pages/Solutions/SectionHighlight"

import { Container } from "./style"
import SEO from "../../components/utils/SEO"
import { translate } from "../../components/utils/translate"
import useScript from "../../hooks/external-scripts"

function SolutionTemplate(
	props: PageProps<object, { solution: ISolutionPage }>
) {
	const modalUrl =
		"new RDStationForms('continuous-k-7194ecf2140fc243203f', 'UA-156947544-1').createForm()"

	const modalPlus =
		"new RDStationForms('continuous-k-plus-446f2b65e883c248f7e6', 'UA-156947544-1').createForm()"

	const modalEn =
		"new RDStationForms('continuous-k-en-5aa555e37de736b6b0f2', 'UA-156947544-1').createForm()"

	const modalEnPlus =
		"new RDStationForms('continuous-k-plus-en-8b4292a5f5ce41f9fdc2', 'UA-156947544-1').createForm()"

	useScript(
		"https://d335luupugsy2.cloudfront.net/js/rdstation-forms/stable/rdstation-forms.min.js",
		true
	)
	useScript(modalUrl, false)
	useScript(modalPlus, false)
	useScript(modalEn, false)
	useScript(modalEnPlus, false)

	const solution: ISolutionPage = props.pageContext.solution
	const { methodContent } = solution
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "solutions",
			name: translate("breadCrumb.solutions"),
			isActive: true,
		},
		{
			url: translate(`solutions.${solution.title}.title`),
			name: translate(`solutions.${solution.title}.title`),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title={solution.title}
				image={solution.thumbnailUrl}
				titleMetaDescription={translate(
					`solutions.${solution.title}.titleMetaDescription`
				)}
			/>
			<Banner titlePage={translate(`menu.${solution.title}`)} />

			<Container
				className="container"
				dangerouslySetInnerHTML={{
					__html: translate(`solutions.${solution.title}.content`),
				}}
			/>

			{methodContent !== null && (
				<SectionHighlight
					methodContent={translate(`solutions.${solution.title}.methodContent`)}
					advantageContent={translate(
						`solutions.${solution.title}.advantageContent`
					)}
				/>
			)}

			<Container className="container">
				<BookCall />
			</Container>
		</Layout>
	)
}

export default SolutionTemplate
