import styled from "styled-components"

export const Container = styled.div`
	padding: 90px 0;
	margin: 48px 0;
	background-color: #13414f;
	background-image: url(https://www.kivalitaconsulting.com/wp-content/uploads/2019/06/projetos-qualidade-img-2-bg.jpg);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;

	section .wrapper-content {
		background-color: #fff;
		border-radius: 5px;
		height: 100%;
		color: #444;
		font-family: "Montserrat", Sans-serif;
		font-size: 17px;
		font-weight: 300;
		line-height: 1.6em;
		padding: 32px;

		h2 {
			color: #444;
			font-family: "Montserrat", Sans-serif;
			font-size: 25px;
			font-weight: 600;
			margin-top: 24px;
			position: relative;
			margin-bottom: 60px;

			::before {
				content: "";
				margin: 0 auto;
				position: absolute;
				height: 2px;
				width: 50px;
				background-color: #f4bf8c;
				bottom: -30px;
				left: 0;
				right: 0;
			}
		}

		ul {
			text-align: left;
			margin-left: 24px;

			li {
				margin-top: 5px;
				padding-bottom: 5px;
			}
		}
	}
`

