import React from "react"
import { Link } from "gatsby"

export interface IButton {
	title: string
	bgColor: string
	textColor: string
	fontFamily: string
	padding: string
	fontSize: number
	fontWeight: number
	id?: string
	dataToggle?: string
	dataTarget?: string
	width?: string
}

const Button = (props: IButton) => {
	return (
		<>
			{props.title.includes("quote") || props.title.includes("orçamento") ? (
				<Link to="/orcamento">
					<button
						className="btn btn-cta"
						style={{
							backgroundColor: `${props.bgColor}`,
							color: `${props.textColor}`,
							fontFamily: `${props.fontFamily}, sans-serif`,
							padding: `${props.padding}`,
							fontSize: `${props.fontSize}px`,
							fontWeight: `${props.fontWeight}`,
							width: `${props.width}`,
						}}
						id={props.id}
						type="button"
						data-toggle={props.dataToggle}
						data-target={props.dataTarget}
					>
						{props.title}
					</button>
				</Link>
			) : (
				<button
					className="btn btn-cta"
					style={{
						backgroundColor: `${props.bgColor}`,
						color: `${props.textColor}`,
						fontFamily: `${props.fontFamily}, sans-serif`,
						padding: `${props.padding}`,
						fontSize: `${props.fontSize}px`,
						fontWeight: `${props.fontWeight}`,
						width: `${props.width}`,
					}}
					id={props.id}
					type="button"
					data-toggle={props.dataToggle}
					data-target={props.dataTarget}
				>
					{props.title}
				</button>
			)}
		</>
	)
}

export default Button
