import React from "react"

import medalIcon from "../../../../assets/medal_icon.svg"
import bestIcon from "../../../../assets/best_icon.svg"

import { Container } from "./styles"
import { translate } from "../../../utils/translate"

interface IProps {
	methodContent: string
	advantageContent: string
}

const SectionHighlight = ({ methodContent, advantageContent }: IProps) => {
	return (
		<Container>
			<div className="container">
				<div className="row">
					<section className="col-md-12 col-lg-5 px-md-4 mb-4">
						<div className="wrapper-content p-3 text-center">
							<img src={bestIcon} width="100px" height="100px" alt="Método" />
	<h2>{translate('solutions.method')}</h2>
							<div dangerouslySetInnerHTML={{ __html: methodContent }}></div>
						</div>
					</section>

					<section className="col-md-12 col-lg-7 px-md-4 mb-4">
						<div className="wrapper-content text-center">
							<img
								src={medalIcon}
								width="100px"
								height="100px"
								alt="Beneficio"
							/>
							<h2>{translate('solutions.advantage')}</h2>
							<div dangerouslySetInnerHTML={{ __html: advantageContent }}></div>
						</div>
					</section>
				</div>
			</div>
		</Container>
	)
}

export default SectionHighlight
